// Gammel style for notice og warning. Kan slettes etter hvert.
// --------------------------------------------------------------
.notice_container,
.warning_container {
    position: relative;
    @include rem('padding', 63px 21px 7px);
    @include rem('border', 2px solid $spindle);
    background-color: $water;
    @include border-radius();
    @include box-sizing($boxmodel: content-box);
    
    &:before {
        content: "";
        position: absolute;
        @include rem('top', 21px);
        @include rem('left', 16px);      
        background: url($main_sprite_url) no-repeat -160px -192px transparent; //.notice_icon
        @include rem('height', 32px);
        @include rem('width', 32px);
    }
    
    .notice_inner,
    .warning_inner {
        @include clearfix();
        @include rem('max-width', 600px);
        @include rem('margin-bottom', 7px);
            
        h1, h2, h3, h4, h5, h6 {
            margin-top: 0;
        }
    
        a {
            text-decoration: underline;
        }

        &__actions {
            display: flex;
            align-items: baseline;
            justify-content:flex-end;
            flex-wrap: wrap-reverse;

            & a {
                @include rem('margin-left', 24px);
            }
            & button {
                @include rem('font-size', 20px);
                line-height: ( 28px / 20px );
                @include rem('margin-left', 24px);
            }
        }
    }
    
    @media (min-width: $md_break) {
        @include rem('padding-top', 21px);
        @include rem('padding-left', 116px);
        @include rem('min-height', 88px);
        margin-top: 0;
        
        &:before {
            @include rem('top', 25px);
            @include rem('left', 26px);
            background-position: 0 -192px; // .notice_icon.large_size
            @include rem('height', 64px);
            @include rem('width', 64px);
        }
    
        .notice_inner,
        .warning_inner {
            
            p, ul {
                @include rem('margin-bottom', 14px);
            }
        }
    }
}


// Warning special
// -------------------------
.warning_container {
    background-color: $white;
    border-color: $main_red;

    &:before {
        @include rem('left', 19px);        
        background-position: -160px -224px; // .warning_icon
    }
    
    @media (min-width: $md_break) {
        @include rem('padding-left', 130px);
        
        &:before {
            @include rem('left', 17px);
            background-position: -64px -192px; // .warning_icon.large_size
            @include rem('width', 96px);
        }
    }
    
}


// Small notice
// -------------------------
.small_notice_container {
    position: relative;
    @include rem('padding', 21px);
    @include rem('border', 1px solid $spindle);
    background-color: $water;
    @include border-radius();
    @include rem('margin-top', 14px);
    
    &:before {
        content: "";
        position: absolute;
        @include rem('top', -16px);
        @include rem('left', 5px);
        background: url($main_sprite_url) no-repeat -160px -192px  transparent; //.notice_icon
        @include rem('height', 32px);
        @include rem('width', 32px);
    }
    
    a {
        text-decoration: underline;
    }
}


// Liste av barn / Underavdelinger
// -------------------------------------------
.filter_search_input_container {
    position: relative;
    @include rem('max-width', 480px);

    .filter_search_label {
        position: absolute;
        @include rem('top', 6px);
        @include rem('left', 4px);
        margin: 0;
        
        &:before {
            content: '';
            @include rem('height', 32px);
            @include rem('width', 32px);
        }
        @include pseudo-icon($extend: ".icon-searchglass--small",
        $position: 'before');
    }

    .filter_search_input {
        width: 100%;
        @include rem('margin', 0 0 14px);
        @include rem('padding-left', 42px);
        @include rem('padding-right', 42px);
        @include rem('border', 1px solid $form_main_color);
    }

    .clear_search_button {
        position: absolute;
        @include rem('top', 7px);
        @include rem('right', 8px);
        padding: 0;
        margin: 0;
        border: 0 none;
        background-color: transparent;
        
        &:before {
            content: '';
            @include rem('height', 32px);
            @include rem('width', 32px);
        }
        @include pseudo-icon($extend: ".icon-close--small",
        $position: 'before');
    }
}

// Remove default input x
// IE10+
.filter_search_input_container .filter_search_input[type=search]::-ms-clear {
    display: none;
}

// Chrome
.filter_search_input_container .filter_search_input[type=search]::-webkit-search-decoration,
.filter_search_input_container .filter_search_input[type=search]::-webkit-search-cancel-button,
.filter_search_input_container .filter_search_input[type=search]::-webkit-search-results-button,
.filter_search_input_container .filter_search_input[type=search]::-webkit-search-results-decoration {
    display: none;
}

.filter_search_input_container + .filter_shortcut_container {
    margin-top: 0;
}

// No max width
.filter_search_input_container.no_max_width {
    max-width: inherit;
}


// Politikere-utvalg har gammel HTML ved bytte til grunt-icons. Derfor en midlertidig fallback
// add_holidays_link er fjernet i ny metode
.important_link.add_holidays_link:before {
    position: absolute;
    @include rem('top', 9px);
    @include rem('left', 5px);
    content: "";
    @include rem('width', 32px);
    @include rem('height', 32px);
    background: url($main_sprite_url) no-repeat -160px -161px transparent; // .calendar_icon
}


// Min side - representation lists
// ---------------------------------------
.representation_list li a .icon {
    position: absolute;
    @include rem('top', 17px);
    @include rem('left', -3px);
}


// Kontakt-ikon som f.eks et institutt paa fysioterapi-siden
